.App {
  font-family: "Roboto", sans-serif;
  text-align: center;
}

.leaflet-container {
  height: 100%;
  width: 100%;
}

.leaflet-div-icon {
  background: transparent;
  border: none;
}

.dropzone {
  cursor: pointer;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-width: 2px;
  border-radius: 2px;
  border-color: #eeeeee;
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border 0.24s ease-in-out;
}

.dropzone:focus {
  border-color: #2196f3;
}

.leaflet-control-layers-toggle {
  background-image: url("//cdnjs.cloudflare.com/ajax/libs/leaflet/1.6.0/images/layers.png") !important;
}
